import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataServiceService } from '../services/data-service.service';

@Component({
  selector: 'app-pais',
  templateUrl: './pais.page.html',
  styleUrls: ['./pais.page.scss'],
})
export class PaisPage implements OnInit {
  public dadesPais: any = null;

  constructor(private activatedRoute: ActivatedRoute, private dades: DataServiceService) { }

  ngOnInit() {
    const title = this.activatedRoute.snapshot.paramMap.get('id');
    const decodedTitle = decodeURIComponent(title);

    this.dades.dadesPais(decodedTitle).subscribe(
      (data: any)=>{
        if (data[0] != null){
          this.dadesPais = data;
          this.dadesPais = this.dadesPais.reverse();
        }
      }
    );
  }

}
